.containerDemo {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;

  .demo {
    max-height: 320px;
    min-height: 320px;
    margin-top: 46px;
    padding: 0 24px;
    gap: 41px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    user-select: none;
    @media screen and (min-width: 988px) {
      height: 100%;
    }
    @media screen and (max-width: 320px) {
      //margin-top: initial;
    }

    .animateBodyText {
      transform: translateY(-80px); /* Анимация поднятия bodyText на 50px вверх */
    }

    .animateBodyTextDont {
      transform: translateY(80px); /* Анимация поднятия bodyText на 50px вверх */
    }

    .wrapCheck {
      height: 50px;
      z-index: 200;
    }

    .bodyText {
      margin: 0 8px;
      max-width: 220px;
      transition: transform 1.5s ease;
      text-align: center;
      position: relative;
      padding: 12px 12px 0 12px;
      gap: 8px;
      display: flex;
      flex-direction: column;
      align-items: center;
      //background: white;
      border-radius: 16px;
      border: 3px solid #3A3838;
      background: #000;

      .text {
        color: #FFF;
        text-align: center;
        font-family: Beeline Sans;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        @media screen and (min-width: 988px){
          font-size: 18px;
        }
      }

      &.bodyTextUp {
        opacity: 0.7;
      }

      .touch {
        position: absolute;
        right: -20px;
        top: 32px;
      }

      .mainPage {
        width: 72px;
        pointer-events: none;
        align-items: center
      }

      &:before {
        content: url("../../img/vectorDemo.svg");
        position: absolute;
        stroke: #F0B900;;
        width: 50px;
        top: -30px;
      }

      &:after {
        width: 50px;
        content: url("../../img/vectorDemo.svg");
        transform: rotate(180deg);
        position: absolute;
        bottom: -30px;
      }
    }

    .itHappens {
      max-width: 100px;
      width: max-content;
      padding: 16px 94px;
      justify-content: center;
      align-items: center;
      gap: 10px;
      z-index: 200;
      text-align: center;

      background: #F0B900;
      color: #202020;
      leading-trim: both;
      text-edge: cap;
      font-family: Beeline Sans;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      position: relative;
      border-radius: 12px;
      @media screen and (max-width: 320px) {
        .text {
          width: 80px;
        }
        padding: 16px 74px;
      }

      &.itHappensUp {
        font-size: 16px;
      }
      @media screen and (min-width: 988px){
        font-size: 18px;
        &.itHappensUp {
          font-size: 20px;
        }
      }
    }

    .itNotHappens {
      max-width: 100px;
      //z-index: 200;
      width: max-content;
      padding: 16px 85px;
      justify-content: center;
      align-items: center;
      gap: 10px;
      border-radius: 12px;
      text-align: center;

      background: white;
      color: #202020;
      leading-trim: both;
      text-edge: cap;
      font-family: Beeline Sans;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      @media screen and (max-width: 320px) {
        padding: 16px 65px;
        .text {
          width: 100px;
        }
      }

      &.itNotHappensUp {
        font-size: 16px;
      }
      @media screen and (min-width: 988px){
        font-size: 18px;
        padding: 16px 85px;
        max-width: inherit;
        &.itNotHappensUp {
          font-size: 20px;
        }
      }
    }
  }

  .blockNext {
    margin-bottom: 52px;
    .textMain {
      padding: 24px;
      color: #FFF;
      font-family: Beeline Sans;
      font-size: 17px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      @media screen and (min-width: 988px){
        font-size: 21px;
      }
    }

    .wrapNext {
      display: flex;
      justify-content: center;

      .next {
        color: #171818;
        leading-trim: both;
        text-edge: cap;
        font-family: Beeline Sans;
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;


        cursor: pointer;
        border: inherit;
        border-radius: 16px;
        padding: 20px 64px;
        background: #F0B900;
        width: max-content;
        @media screen and (min-width: 988px){
          font-size: 22px;
        }
      }
    }
  }
  @media screen and (max-width: 400px){
    margin-top: inherit;
  }
}
