.factsBody{
  height: 100%;
  gap: 65px;
  display: flex;
  z-index: 2;
  flex-direction: column;
  justify-content: center;
  .containerResult{
    display: flex;
    flex-direction:column;
    gap: 15px;
  }
  .answer{
    color: #FFF;
    margin-left: 20px;
    font-family: Beeline Sans;
    font-size: 26px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    width: max-content;
    border-bottom: 1px solid #F0B900;
    @media screen and (min-width: 988px){
      font-size: 30px;
    }
  }
  .bodyResult{
    border-radius: 16px;
    border: 3px solid #3A3838;
    color: #FFFFFF;
    leading-trim: both;
    text-edge: cap;
    font-family: Beeline Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    position: relative;
    padding: 20px 0 0 20px;
    gap: 8px;

    display: flex;
    flex-direction: column;
    align-items: flex-end;
    span{
      padding-right: 20px;
    }
    @media screen and (min-width: 988px){
      font-size: 24px;
    }
    img{
      max-height: 160px;
      align-items: center;
      @media screen and (min-height: 651px) {
        max-height: 190px;
      }
      @media screen and (min-height: 1000px) {
        max-height: 220px;
      }
    }
  }
  a {
    display: flex;
    justify-content: center;
    text-decoration: none;
    .next {
      cursor: pointer;
      border: inherit;
      width: max-content;
      border-radius: 16px;
      padding: 20px 80px;
      background: #F0B900;
      color: #202020;
      leading-trim: both;
      text-edge: cap;
      font-family: Beeline Sans;
      font-size: 18px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      @media screen and (min-width: 988px){
        font-size: 22px;
      }
    }
  }
}
