body{
  background: #171818;
  display: flex;
  align-items: center

}
.App{
  display: flex;
  background: #171818;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
}
.container{
  background: #171818;
  max-width: 450px;
  height: 100%;

}
@media screen and (min-width: 988px) {
  .container{
    border: 1px solid #F0B900;
    /* padding: 18px 0; */
    border-radius: 16px;
    height: calc(100% - 60px);
  }
  body{
    height: 100vh;
  }
}
